/* eslint-disable react-hooks/rules-of-hooks */
import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Input } from "@/components/ui/input";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

type UserRow = {
  id: number;
  user: string;
  gender: string;
  addedBy: string;
  type: string;
  serverId: string;
};

const page = () => {
  const [data, setData] = useState<UserRow[]>([
    { id: 3, user: "User", gender: "Female", addedBy: "Nassim", type: "admin", serverId: "admin" },
    { id: 4, user: "Nessim", gender: "Male", addedBy: "User", type: "admin", serverId: "admin" },
    { id: 1004, user: "Kayondo", gender: "Male", addedBy: "User", type: "normal", serverId: "normal" },
    { id: 2004, user: "John", gender: "Male", addedBy: "User", type: "normal", serverId: "normal" },
    { id: 3004, user: "Werps", gender: "Male", addedBy: "User", type: "normal", serverId: "normal" },
  ]);

  const [editRow, setEditRow] = useState<UserRow | null>(null);

  const handleEdit = (id: number) => {
    const row = data.find((d) => d.id === id);
    if (row) setEditRow(row);
  };

  const handleDelete = (id: number) => {
    setData(data.filter((row) => row.id !== id));
  };

  const handleSave = () => {
    if (editRow) {
      setData(data.map((row) => (row.id === editRow.id ? editRow : row)));
      setEditRow(null); // Close the sheet
    }
  };

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-4">
        <Button>Add</Button>
        <Button variant="outline">Print</Button>
      </div>

      <Table>
        <TableCaption>A list of users and their details</TableCaption>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead>User</TableHead>
            <TableHead>Gender</TableHead>
            <TableHead>Added By</TableHead>
            <TableHead>Type</TableHead>
            <TableHead>Server ID</TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {data.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.user}</TableCell>
              <TableCell>{row.gender}</TableCell>
              <TableCell>{row.addedBy}</TableCell>
              <TableCell>{row.type}</TableCell>
              <TableCell>{row.serverId}</TableCell>
              <TableCell>
                <div className="flex space-x-2">
                  <Sheet>
                    <SheetTrigger asChild>
                      <Button size="sm" onClick={() => handleEdit(row.id)}>Edit</Button>
                    </SheetTrigger>
                    <SheetContent>
                      <div className="p-4">
                        <h2 className="text-lg font-bold mb-4">Edit Row</h2>
                        {editRow && (
                          <>
                            <div className="mb-4">
                              <label>ID:</label>
                              <Input
                                value={editRow.id}
                                disabled
                                className="w-full mt-1"
                              />
                            </div>
                            <div className="mb-4">
                              <label>User:</label>
                              <Input
                                value={editRow.user}
                                onChange={(e) => setEditRow({ ...editRow, user: e.target.value })}
                                className="w-full mt-1"
                              />
                            </div>
                            <div className="mb-4">
                              <label>Gender:</label>
                              <Input
                                value={editRow.gender}
                                onChange={(e) => setEditRow({ ...editRow, gender: e.target.value })}
                                className="w-full mt-1"
                              />
                            </div>
                            <Button onClick={handleSave} className="mr-2">Save</Button>
                          </>
                        )}
                      </div>
                    </SheetContent>
                  </Sheet>
                  <Button
                    size="sm"
                    variant="destructive"
                    onClick={() => handleDelete(row.id)}
                  >
                    Delete
                  </Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default page;
